import React, { Component } from 'react';
import Layout from './containers/Layout/Layout';
import Aux from './hoc/Auxiliary/Auxiliary';
import Side from './components/Side/Side';
import AboutMe from './components/AboutMe/AboutMe';
import Skills from './components/Skills/Skills';
import Work from './components/Work/Work';
import ContactMe from './components/ContactMe/ContactMe';
import TurnContext from './context/turn-context';

class App extends Component {
  state = {
    downClicked: false,
    navSections: {
      'aboutMe': true,
      'skills': false,
      'work': false,
      'contactMe': false,
    },
  }

  //TODO(1): Intro Scroll Down Handler
  scrollDownHandler = () => {
    this.setState({downClicked: true});
  }

  //TODO(2): Turn section handler
  turnSectionHandler = section => {
    const sections = {
      ...this.state.navSections
    }
    Object.keys(sections).forEach(sec => sections[sec] = false);

    // Setting the state of the selected section to be shown
    sections[section] = true;

    // Setting the new state
    this.setState({navSections: { ...sections }});
  }


  render() {
    return (
      <Aux>
        <TurnContext.Provider
          value={{
            turnSection: this.turnSectionHandler,
            sections: this.state.navSections
            }}>
          <Side scrollDown={this.scrollDownHandler} downClicked={this.state.downClicked}/>
        </TurnContext.Provider>
        <Layout downClicked={this.state.downClicked}>
          <AboutMe isActive={this.state.navSections.aboutMe}/>
          <Skills isActive={this.state.navSections.skills}/>
          <Work isActive={this.state.navSections.work}/>
          <ContactMe isActive={this.state.navSections.contactMe}/>
        </Layout>
      </Aux>
      
    );
  }
}

export default App;



