import React from 'react';
import classes from './Skills.css';

const skills = props => {
  const skillsClasses = [classes.Skills];
  if (props.isActive) skillsClasses.push(classes.shown)

  return (
    <section className={skillsClasses.join(' ')}>
      <h1>Skills</h1>
    </section>
  )
}

export default skills;


