import React from 'react';
import classes from './IntroItems.css';

const introItems = props => {
  const introClasses = [classes.IntroItems];

  if (props.downClicked) introClasses.push(classes.goDown)

  return (
    <div className={introClasses.join(' ')} >
      <h3>Hello, I am</h3>
      <h1>Bahaa Mohamed</h1>
      <p>Front-End Web Developer</p>
    </div>
  )
}

export default introItems;


