import React from 'react';
import classes from './Intro.css';

const intro = props => {
  const introClasses = [classes.Intro];

  if (props.downClicked) introClasses.push(classes.goDown)

  return (
    <section className={introClasses.join(' ')}>
      
    </section>
  );
}

export default intro;


