import React from 'react';
import classes from './Side.css';
import IntroItems from './IntroItems/IntroItems';
import Navigation from './Navigation/Navigation';

const side = props => {
  const downSectionClasses = [classes.downSection];
  if (props.downClicked) downSectionClasses.push(classes.goDown)

  return (
    <aside className={classes.Side}>
      <IntroItems downClicked={props.downClicked} />

      <div
        className={downSectionClasses.join(' ')}
        onClick={props.scrollDown}
        >
        <div>Click Here</div>
        <svg
          xmlns="http://www.w3.org/2000/svg"
          enableBackground="new 0 0 24 24"
          height="30px"
          viewBox="0 0 24 24"
          width="30px"
          fill="var(--white)"
          className={classes.downIcon}>
          <g>
            <rect fill="none" height="24" width="24"/>
            <path d="M12,4c4.41,0,8,3.59,8,8s-3.59,8-8,8s-8-3.59-8-8S7.59,4,12,4 M12,2C6.48,2,2,6.48,2,12c0,5.52,4.48,10,10,10 c5.52,0,10-4.48,10-10C22,6.48,17.52,2,12,2L12,2z M13,12l0-3c0-0.55-0.45-1-1-1h0c-0.55,0-1,0.45-1,1l0,3H9.21 c-0.45,0-0.67,0.54-0.35,0.85l2.79,2.79c0.2,0.2,0.51,0.2,0.71,0l2.79-2.79c0.31-0.31,0.09-0.85-0.35-0.85H13z"/>
          </g>
        </svg>
      </div>
      
      <Navigation downClicked={props.downClicked}/>
    </aside>
  )
}

export default side;


