import React, { useContext } from 'react';
import classes from './NavItem.css'
import TurnContext from '../../../../context/turn-context';

const navItem = props => {
  const turnContext = useContext(TurnContext);

  const navItemClasses = [classes.NavItem];
  if (turnContext.sections[props.data]) navItemClasses.push(classes.active)

  return (
    <a
      className={navItemClasses.join(' ')}
      onClick={() => turnContext.turnSection(props.data)}
      >
      <img src={props.icon} alt={props.data.split('M').join(' m')}/>
    </a>
  )
}

export default navItem;


